<template lang="html">
    <div class="block">
      <table width='100%' border='0' cellSpacing='3' cellPadding='5' style=" background-color: #64abe1;">
        <tr>
          <div class='sub_tit'>
              氣氣氣氣氣像查詢拉拉拉拉拉拉~
          </div>
          <table width="100%" border="0" cellSpacing="0" cellPadding="0" class="tb_style">
              <tbody>
                  <tr >
                      <td width="10%">選個地區:</td>
                      <td> 
                          <div class='selectbox2'>
                            <select v-model="area" v-on:change="testCall">
                                <option v-for="list in  areaList" :value="list">{{list}}</option>
                            </select>
                          </div>
                      </td>
                  </tr>
              </tbody>
          </table>
        </tr>
      </table>
      <br><br><br>
      <div class='sub_tit' v-if="isShow">
        氣象資料
      </div>
      <table width="100%" border="0" cellSpacing="0" cellPadding="0" class="tb_style" v-if="isShow">
          <tbody>
              <tr style="background-color: #f39121;">
                <th width="20%">日期時段</th>
                <th>感受</th>
                <th>氣溫</th>
                <th>降雨機率</th>
                <th>天氣狀況</th>
              </tr>
              <tr>
                  <td>{{Wx[0].startTime+'~'+Wx[0].endTime}}</td>
                  <td align="center">{{CI[0].parameter.parameterName}}</td>
                  <td align="center">{{MinT[0].parameter.parameterName+'~'+MaxT[0].parameter.parameterName}}℃</td>
                  <td align="center">{{PoP[0].parameter.parameterName}}%</td>
                  <td align="center">{{Wx[0].parameter.parameterName}}<img :src="getIcon(Wx[0].parameter.parameterValue,Wx[0].endTime)"  width="70px" /></td>
              </tr>
              <tr class="bg">
                  <td>{{Wx[1].startTime+'~'+Wx[1].endTime}}</td>
                  <td align="center">{{CI[1].parameter.parameterName}}</td>
                  <td align="center">{{MinT[1].parameter.parameterName+'~'+MaxT[1].parameter.parameterName}}℃</td>
                  <td align="center">{{PoP[1].parameter.parameterName}}%</td>
                  <td align="center">{{Wx[1].parameter.parameterName}}<img :src="getIcon(Wx[1].parameter.parameterValue,Wx[1].endTime)"  width="70px" /></td>
              </tr>
              <tr>
                  <td>{{Wx[2].startTime+'~'+Wx[2].endTime}}</td>
                  <td align="center">{{CI[1].parameter.parameterName}}</td>
                  <td align="center">{{MinT[2].parameter.parameterName+'~'+MaxT[2].parameter.parameterName}}℃</td>
                  <td align="center">{{PoP[2].parameter.parameterName}}%</td>
                  <td align="center">{{Wx[2].parameter.parameterName}}<img :src="getIcon(Wx[2].parameter.parameterValue,Wx[2].endTime)"  width="70px" /></td>
              </tr>
          </tbody>
      </table>
    </div>
</template>





<script>
import DayThunderstorm  from '../images/day-thunderstorm.svg';
import DayClear  from '../images/day-clear.svg';
import DayCloudyFog  from '../images/day-cloudy-fog.svg';
import DayCloudy  from '../images/day-cloudy.svg';
import DayFog  from '../images/day-fog.svg';
import DayPartiallyClearWithRain  from '../images/day-partially-clear-with-rain.svg';
import DaySnowing  from '../images/day-snowing.svg';
import NightThunderstorm  from '../images/night-thunderstorm.svg';
import NightClear  from '../images/night-clear.svg';
import NightCloudyFog  from '../images/night-cloudy-fog.svg';
import NightCloudy  from '../images/night-cloudy.svg';
import NightFog  from '../images/night-fog.svg';
import NightPartiallyClearWithRain  from '../images/night-partially-clear-with-rain.svg';
import NightSnowing  from '../images/night-snowing.svg';


export default {
  name: 'meteorological',
  data () {
    return {
      area: '臺中市',
      areaList:["基隆市",
        "嘉義市",
        "臺北市",
        "嘉義縣",
        "新北市",
        "臺南市",
        "桃園縣",
        "高雄市",
        "新竹市",
        "屏東縣",
        "新竹縣",
        "臺東縣",
        "苗栗縣",
        "花蓮縣",
        "臺中市",
        "宜蘭縣",
        "彰化縣",
        "澎湖縣",
        "南投縣",
        "金門縣",
        "雲林縣",
        "連江縣"],
      locationName:"",
      Wx:[],
      PoP:[],
      MinT:[],
      CI:[],
      MaxT:[],
      isShow:false,
      isThunderstorm: ["15", "16", "17", "18", "21", "22", "33", "34", "35", "36", "41"],
      isClear: ["1"],
      isCloudyFog: ["25", "26", "27", "28"],
      isCloudy: ["2", "3", "4", "5", "6", "7"],
      isFog: ["24"],
      isPartiallyClearWithRain: [
        "8", "9", '10', "11", "12",
        "13", "14", "19", "20", "29", '30',
        "31", "32", '38', "39",
      ],
      isSnowing: ["23", '37', "42"],
      night:{
        'Thunderstorm':NightThunderstorm,
        'Clear':NightClear,
        'CloudyFog':NightCloudyFog,
        'Cloudy':NightFog,
        'Fog':NightFog,
        'PartiallyClearWithRain':NightPartiallyClearWithRain,
        'Snowing':NightSnowing,
      },
      day:{
        'Thunderstorm':DayThunderstorm,
        'Clear':DayClear,
        'CloudyFog':DayCloudyFog,
        'Cloudy':DayCloudy,
        'Fog':DayFog,
        'PartiallyClearWithRain':DayPartiallyClearWithRain,
        'Snowing':DaySnowing,
      }
    }
  },
  methods:{
    testCall(){
        let url = "https://opendata.cwb.gov.tw/api/v1/rest/datastore/F-C0032-001?Authorization=CWB-8E560570-BFD5-4785-85F5-B97DC3540ACC&locationName="+encodeURIComponent(this.area)
        this.$axios.get(url)
        .then(response => {
            console.log(response.data.records.location[0])
            let e = response.data.records.location[0] ;
            this.locationName = e.locationName;
            e.weatherElement.forEach(element =>{ 
              this[element.elementName]=element.time;
            });
            this.isShow=true;
        })
        .catch(error => {
            console.log(error)
        })
    },
    getIcon(value,type){
      console.log(type);

      var getdate = new Date(type );
      //var h = new Date().format(type)  ;
      if(getdate.getHours() == 18){
        type='day';
      }else{
        type='night';
      }
      var wi = '';
      if(this.isThunderstorm.indexOf(value)>=0){
        wi = 'Thunderstorm';
      }
      if(this.isClear.indexOf(value)>=0){
        wi = 'Clear';
      }
      if(this.isCloudyFog.indexOf(value)>=0){
        wi = 'CloudyFog';
      }
      if(this.isCloudy.indexOf(value)>=0){
        wi = 'Cloudy';
      }
      if(this.isFog.indexOf(value)>=0){
        wi = 'Fog';
      }
      if(this.isPartiallyClearWithRain.indexOf(value)>=0){
        wi = 'PartiallyClearWithRain';
      }
      if(this.isSnowing.indexOf(value)>=0){
        wi = 'Snowing';
      }

      return this[type][wi];

    }
  },
  mounted(){
    this.testCall();
  },
  
}
</script>



